	module.exports = {"data":{"markdownRemark":{"html":"<!-- データレイヤ変数 -->\n<script>\nvar userTypes = ['A','B','C'];\nvar userType = userTypes[Math.floor(Math.random() * userTypes.length)];\nwindow.dataLayer = window.dataLayer || [];\ndataLayer.push({'userType': userType});\n</script>\n<h1 id=\"gtm-banner-example\"><a href=\"#gtm-banner-example\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>gtm-banner-example</h1>\n<p>GTM で Banner 配信を行う example ページです。このページ上には、データレイヤ変数として <code>userType</code> が設定されています。本来は、サイトを訪れた人の状態を元に判断して、設定すべきですが、便宜上ランダムで <code>A</code>、<code>B</code>、<code>C</code> の中から選択しています。     </p>\n<p>GTM では、<code>userType</code> データレイヤ変数を値により、設定する Banner の切り替えを行なっています。以下に広告枠として、<code>&#x3C;div id=\"gtm_banner\">&#x3C;/div></code> を設定しています。この欄が、<code>userType</code> により切り替わります。    </p>\n<ul>\n<li>\n<p><strong>Banner 枠</strong>    </p>\n<div id=\"gtm-banner-container\"></div>\n</li>\n</ul>","timeToRead":1,"excerpt":"gtm-banner-example GTM で Banner 配信を行う example…","frontmatter":{"title":"gtm-banner-example","cover":"https://drive.google.com/uc?export=view&id=1Qvz0x_xo-Ct8iwgs6BXPdTQ2HTtwYQW2","date":"25/11/2017","category":"GTM","tags":["GTM","advertisement"]},"fields":{"nextTitle":"gtm-banner-example","nextSlug":"/gtm-banner-example","prevTitle":"gtm-banner-example","prevSlug":"/gtm-banner-example","slug":"/gtm-banner-example"}}},"pathContext":{"slug":"/gtm-banner-example"}}

